"use client";

import { useEffect, useState } from "react";
import { collection, getDocs, query, doc, getDoc } from "firebase/firestore";
import { db } from "@/lib/firebase";

interface Movement {
  id: string;
  title: string;
  date: string;
  description: string;
  imageUrl?: string;
}

interface FeatureBlock {
  category: string;
  title: string;
  subtitle: string;
  imageUrl: string;
}

interface HomeContent {
  hero: FeatureBlock;
  subFeature1: FeatureBlock;
  subFeature2: FeatureBlock;
}

export default function Home() {
  const [movements, setMovements] = useState<Movement[]>([]);
  const [content, setContent] = useState<HomeContent | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch Movements
        const q = query(collection(db, "movements"));
        const movementsSnap = await getDocs(q);
        const movementsData = movementsSnap.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        })) as Movement[];
        setMovements(movementsData);

        // Fetch Home Content
        const docRef = doc(db, "content", "home");
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          setContent(docSnap.data() as HomeContent);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  return (
    <div className="min-h-screen pt-32 pb-12 px-4 md:px-8">

      {/* Disclaimer / Mobile Header Alternative */}
      <section className="mb-12 md:hidden">
        <h1 className="text-6xl font-black uppercase tracking-tighter leading-none">
          Nava<br />Fundacion
        </h1>
      </section>

      <div className="grid grid-cols-1 md:grid-cols-12 gap-8 relative">

        {/* Main Content Area (Left 2/3 - Spans 8 cols) */}
        <div className="md:col-span-8 flex flex-col gap-16">

          {/* Featured Block 1 */}
          <div className="flex flex-col gap-2">
            <h2 className="font-mono text-sm uppercase text-gray-400 border-b border-gray-800 pb-2 mb-2">
              {content?.hero.category || "Current Exhibition"}
            </h2>
            <div className="aspect-[16/9] bg-zinc-900 w-full relative group cursor-pointer overflow-hidden">
              {content?.hero.imageUrl ? (
                <img src={content.hero.imageUrl} className="w-full h-full object-cover opacity-80 group-hover:opacity-100 transition-opacity" alt={content.hero.title} />
              ) : (
                <div className="absolute inset-0 flex items-center justify-center text-zinc-700 font-bold uppercase text-2xl group-hover:text-white transition-colors">
                  {content?.hero.title || "Description"}
                </div>
              )}
            </div>
            <h3 className="text-3xl md:text-5xl font-bold uppercase leading-none mt-2">
              {content?.hero.title || "Loading..."}
            </h3>
            <p className="font-mono text-sm md:text-base max-w-xl text-gray-400">
              {content?.hero.subtitle || "Loading description..."}
            </p>
          </div>

          {/* Featured Block 2 */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            <div className="flex flex-col gap-2">
              <h2 className="font-mono text-sm uppercase text-gray-400 border-b border-gray-800 pb-2 mb-2">
                {content?.subFeature1.category || "Publication"}
              </h2>
              <div className="aspect-[4/5] bg-zinc-900 w-full relative overflow-hidden group">
                {content?.subFeature1.imageUrl && (
                  <img src={content.subFeature1.imageUrl} className="w-full h-full object-cover opacity-80 group-hover:opacity-100 transition-opacity" alt={content.subFeature1.title} />
                )}
              </div>
              <h3 className="text-2xl font-bold uppercase leading-none">{content?.subFeature1.title || "Title"}</h3>
              <p className="text-sm text-gray-400">{content?.subFeature1.subtitle || "Subtitle"}</p>
            </div>
            <div className="flex flex-col gap-2">
              <h2 className="font-mono text-sm uppercase text-gray-400 border-b border-gray-800 pb-2 mb-2">
                {content?.subFeature2.category || "Research"}
              </h2>
              <div className="aspect-[4/5] bg-zinc-900 w-full relative overflow-hidden group">
                {content?.subFeature2.imageUrl && (
                  <img src={content.subFeature2.imageUrl} className="w-full h-full object-cover opacity-80 group-hover:opacity-100 transition-opacity" alt={content.subFeature2.title} />
                )}
              </div>
              <h3 className="text-2xl font-bold uppercase leading-none">{content?.subFeature2.title || "Title"}</h3>
              <p className="text-sm text-gray-400">{content?.subFeature2.subtitle || "Subtitle"}</p>
            </div>
          </div>

        </div>

        {/* Sidebar (Right 1/3 - Spans 4 cols) */}
        <div className="md:col-span-4 flex flex-col gap-12 border-l border-zinc-900 md:pl-8">

          {/* Upcoming List */}
          <div>
            <h2 className="font-mono text-sm uppercase text-gray-400 border-b border-gray-800 pb-2 mb-6">Upcoming</h2>

            {loading ? (
              <div className="text-zinc-600">Loading schedule...</div>
            ) : movements.length > 0 ? (
              <div className="flex flex-col gap-6">
                {movements.map((item) => (
                  <div key={item.id} className="group cursor-pointer">
                    <span className="block font-mono text-xs text-nava-green mb-1">{item.date}</span>
                    <h3 className="text-xl font-bold uppercase leading-tight group-hover:underline decoration-1 underline-offset-4">{item.title}</h3>
                    <p className="text-xs text-gray-500 mt-1 line-clamp-1">{item.description}</p>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-zinc-600 text-sm">No upcoming events scheduled.</div>
            )}
          </div>

          {/* Newsletter Mini-Block */}
          <div className="bg-white text-black p-6 mt-auto">
            <h3 className="font-bold uppercase text-xl mb-4 leading-none">Subscribe to our newsletter</h3>
            <input type="email" placeholder="EMAIL ADDRESS" className="w-full border-b border-black bg-transparent py-2 outline-none placeholder:text-zinc-500 mb-4" />
            <button className="text-xs font-bold uppercase border border-black px-4 py-2 hover:bg-black hover:text-white transition-colors">Submit</button>
          </div>

        </div>

      </div>
    </div>
  );
}
